export default function PlanifierSoutenance() {
  const [formData, setFormData] = useState<FormData>(INITIAL_FORM_STATE);
  const [groups, setGroups] = useState<Group[]>([]); 
  const [jurys, setJurys] = useState<Jury[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [toast, setToast] = useState<Toast | null>(null);

  const rooms: Room[] = [
    { id: "1", name: "Salle 12" },
    { id: "2", name: "Salle 42" },
    { id: "3", name: "Salle 21" },
  ];

  // ... Fetching data and other useEffect logic ...

  return (
    <div className="p-12 bg-gray-50 min-h-screen relative">
      {/* Toast Notification */}
      {toast && (
        <div className="fixed top-4 right-4 z-50 animate-fade-in">
          <Alert
            variant={toast.type === "error" ? "destructive" : "default"}
            className="flex items-center justify-between p-4 text-lg"
          >
            <AlertDescription>{toast.message}</AlertDescription>
            <Button
              variant="ghost"
              size="icon"
              className="h-6 w-6"
              onClick={() => setToast(null)}
            >
              <X className="h-6 w-6" />
            </Button>
          </Alert>
        </div>
      )}

      <div className="max-w-7xl mx-auto">
        <h1 className="text-5xl font-bold text-gray-800 mb-12 text-center">
          Planification des Soutenances
        </h1>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Group Selection */}
          <div className="flex justify-center">
            <Card className="p-6 w-full max-w-lg">
              <CardHeader>
                <CardTitle className="flex items-center gap-3 text-xl text-center">
                  <Users className="h-6 w-6" />
                  Groupes
                </CardTitle>
              </CardHeader>
              <CardContent>
                <Select
                  value={formData.group}
                  onValueChange={(value) => setFormData({ ...formData, group: value })}
                  className="text-lg"
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Sélectionner un groupe" />
                  </SelectTrigger>
                  <SelectContent>
                    {groups.map((group) => (
                      <SelectItem key={group.idGroupe} value={group.idGroupe}>
                        {group.nomGroupe} ({group.nbEtudiants} étudiants)
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </CardContent>
            </Card>
          </div>

          {/* Date and Time Selection */}
          <div className="flex justify-center">
            <Card className="p-6 w-full max-w-lg">
              <CardHeader>
                <CardTitle className="flex items-center gap-3 text-xl text-center">
                  <Calendar className="h-6 w-6" />
                  Date et Heure
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                <Input
                  type="date"
                  value={formData.date}
                  onChange={(e) => setFormData({ ...formData, date: e.target.value })}
                  className="text-lg p-3"
                />
                <Input
                  type="time"
                  value={formData.time}
                  onChange={(e) => setFormData({ ...formData, time: e.target.value })}
                  className="text-lg p-3"
                />
              </CardContent>
            </Card>
          </div>

          {/* Room Selection */}
          <div className="flex justify-center">
            <Card className="p-6 w-full max-w-lg">
              <CardHeader>
                <CardTitle className="flex items-center gap-3 text-xl text-center">
                  <Home className="h-6 w-6" />
                  Salle
                </CardTitle>
              </CardHeader>
              <CardContent>
                <Select
                  value={formData.location}
                  onValueChange={(value) => setFormData({ ...formData, location: value })}
                  className="text-lg"
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Sélectionner une salle" />
                  </SelectTrigger>
                  <SelectContent>
                    {rooms.map((room) => (
                      <SelectItem key={room.id} value={room.id}>
                        {room.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </CardContent>
            </Card>
          </div>

          {/* Jury Selection */}
          <div className="flex justify-center">
            <Card className="p-6 w-full max-w-lg">
              <CardHeader>
                <CardTitle className="flex items-center gap-3 text-xl text-center">
                  <User className="h-6 w-6" />
                  Jury
                </CardTitle>
              </CardHeader>
              <CardContent>
                <Select
                  value={formData.jury}
                  onValueChange={(value) => setFormData({ ...formData, jury: value })}
                  className="text-lg"
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Sélectionner un jury" />
                  </SelectTrigger>
                  <SelectContent>
                    {jurys.map((jury) => (
                      <SelectItem key={jury.idJury} value={jury.idJury.toString()}>
                        {jury.nom}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Submit Button */}
        <div className="mt-12 flex justify-center">
          <Button
            size="lg"
            onClick={handleSubmit}
            className="bg-[#1E2837] hover:bg-[#35495E] text-white px-14 py-6 text-2xl rounded-lg"
          >
            Planifier la soutenance
          </Button>
        </div>
      </div>
    </div>
  );
}
